import Vue from 'vue'

export const validForm = {
  //
  phone: (rule, value, callback) => {
    if (rule.required) {
      if (!value) {
        callback(new Error(''));
      } else if (!validForm.pattresult["phone"].test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    }
  },
  //
  tel: (rule, value, callback) => {
    if (!rule.required && value == "") {
      callback();
      return;
    } else {
      if (!validForm.pattresult["tel"].test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    }

  },
  //
  email: (rule, value, callback) => {
    if (!rule.required && value == "") {
      callback();
      return;
    } else {
      if (!validForm.pattresult["email"].test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    }
  },
  //
  chinese: (rule, value, callback) => {
    if (!rule.required && value == "") {
      callback();
      return;
    } else {
      if (!validForm.pattresult["chinese"].test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    }

  },
  //
  array: (rule, value, callback) => {
    if (!rule.required && value == "") {
      callback();
      return;
    } else {
      if (!validForm.pattresult["chinese"] instanceof Array) {
        callback(new Error(''));
      } else {
        callback();
      }
    }

  },
  //
  num: (rule, value, callback) => {
    if (!rule.required && value == "") {
      callback();
      return;
    } else {
      if (!validForm.pattresult["num"].test(value)) {
        callback(new Error(''));
      } else {
        callback();
      }
    }
  },
  // yyyy-mm-dd
  ymd: (rule, value, callback) => {
    if (rule.type == typeof value) {
      if (rule.required) { //
        if (!validForm.pattresult["ymd"].test(value)) {
          callback(new Error(''));
        } else {
          callback();
        }
        ;
      } else {
        callback();
      }
      ;
    } else {
      callback();
    }
    ;
  },
  // 
  password: (rule, value, callback) => {
    if (rule.required) {
      if (!value) {
        callback(new Error(''));
      } else if (value.length < 6) {
        callback(new Error('6'));
      } else {
        callback();
      }
    }
  },
  // 
  postCode: (rule, value, callback) => {
    if (rule.required) {
      if (!value) {
        callback(new Error(''));
      } else if (!validForm.pattresult["postCode"].test(value)) {
        callback(new Error('6'));
      } else {
        callback();
      }
    }
  },
  pattresult: {
    "email": /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,
    "chinese": /^[\u4e00-\u9fa5]{0,}$/,
    "phone": /^1[34578]\d{9}$/,
    "tel": /^([0-9]{3,4}-)?[0-9]{7,8}$/,
    "ymd": /^\d{4}\-\d{2}\-\d{2}$/,
    "num": /^[0-9]*$/,
    'postCode': /^\d{6}$/
  }
};



