<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-date"></i> </el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="form-box">
      <el-form ref="register" :model="ruleForm" :rules="rules" size="medium" label-width="120px" label-position="left">
        <form-title name=""></form-title>
        <el-form-item labelWidth="120px" label="" prop="regType">
          <el-select v-model="ruleForm.regType" placeholder="">
            <el-option
              v-for="item in archives.regType"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-row :gutter="80">
          <el-col :span="12">
            <el-form-item label="()" prop="enterpriseZh">
              <el-input v-model="ruleForm.enterpriseZh" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="()">
              <el-input v-model="ruleForm.enterpriseEn" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="80">
          <el-col :span="12">
            <el-form-item label="()" prop="registZh">
              <el-input v-model="ruleForm.registZh" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="()">
              <el-input v-model="ruleForm.registEn" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="80">
          <el-col :span="12">
            <el-form-item label=")" prop="registPostcode">
              <el-input v-model="ruleForm.registPostcode" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label=")" prop="workPostcode">
              <el-input v-model="ruleForm.workPostcode" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="80">
          <el-col :span="12">
            <el-form-item label="()" prop="workplaceZh">
              <el-input v-model="ruleForm.workplaceZh" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="()">
              <el-input v-model="ruleForm.workplaceEn" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <form-title name=""></form-title>
        <el-row :gutter="40">
          <el-col :span="8">
            <el-form-item label="" prop="adminarea">
              <el-cascader
                :options="regionData"
                v-model="ruleForm.adminarea"
                @change="handleAdminareaChange">
              </el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="adminareaCode">
              <el-input v-model="ruleForm.adminareaCode" placeholder="" disabled clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="80">
          <el-col :span="12">
            <el-form-item label="" prop="licenseCode">
              <el-input v-model="ruleForm.licenseCode" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="7">
            <el-form-item labelWidth="120px" label="" prop="enterpriseType">
              <el-select v-model="ruleForm.enterpriseType" placeholder="">
                <el-option
                  v-for="item in archives.enterpriseType"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="17">
            <el-tag type="danger">260034004200</el-tag>
          </el-col>
        </el-row>
        <el-row :gutter="40">
          <el-col :span="8">
            <el-form-item label="" prop="currency">
              <el-select v-model="ruleForm.currency" placeholder="">
                <el-option
                  v-for="item in archives.currency"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="capital">
              <el-input v-model.number="ruleForm.capital" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="80">
          <el-col :span="7">
            <el-form-item label="">
              <el-select v-model="ruleForm.economicType" placeholder="">
                <el-option
                  v-for="item in archives.economicType"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <form-title name=""></form-title>
        <el-row :gutter="40">
          <el-col :span="8">
            <el-form-item label="" prop="leader">
              <el-input v-model="ruleForm.leader" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="leaderTele">
              <el-input v-model="ruleForm.leaderTele" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="40">
          <el-col :span="8">
            <el-form-item label="" prop="contact">
              <el-input v-model="ruleForm.contact" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="contactTele">
              <el-input v-model="ruleForm.contactTele" placeholder="" disabled></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="40">
          <el-col :span="8">
            <el-form-item label="" prop="contactEmail">
              <el-input v-model="ruleForm.contactEmail" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="" prop="emailReceive">
              <el-radio v-model="ruleForm.emailReceive" v-for="item in archives.emailable" :key="item.id"
                        :label="item.id">{{item.name}}
              </el-radio>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="40">
          <el-col :span="8">
            <el-form-item label="" prop="website">
              <el-input v-model="ruleForm.website" placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="40">
          <el-col :span="8">
            <el-form-item label="" prop="productInfo">
              <el-input v-model="ruleForm.productInfo" type="textarea" :autosize="{minRows:3, maxRows:5}"
                        placeholder="" clearable></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <div>
          <form-title name=""></form-title>
          <el-row :gutter="80">
            <el-col :span="12">
              <el-form-item label="" prop="invoiceTitle">
                <el-input v-model="ruleForm.invoiceTitle" placeholder="" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="taxNumber">
                <el-input v-model="ruleForm.taxNumber" placeholder="" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="80">
            <el-col :span="12">
              <el-form-item label="" prop="taxAddr">
                <el-input v-model="ruleForm.taxAddr" placeholder="" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="openBank">
                <el-input v-model="ruleForm.openBank" placeholder="" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="">
            <el-tag type="info">{{registDate}}</el-tag>
          </el-form-item>
        </div>

        <div class="footer">
          <el-form-item>
            <el-button size="medium" type="primary" @click="onSubmit"></el-button>
            <el-button size="medium" @click="reset"></el-button>
          </el-form-item>
        </div>
      </el-form>
      <el-dialog
        title=""
        :visible.sync="needUpload"
        width="30%"
        center>
        <span></span>
        <span slot="footer" class="dialog-footer">
        <el-button @click="needUpload = false"> </el-button>
        <el-button type="primary" @click="needUpload = false"> </el-button>
      </span>
      </el-dialog>
    </div>
    <back-top></back-top>
  </div>
</template>

<script>
  import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item.vue";
  import ElInput from "../../../node_modules/element-ui/packages/input/src/input.vue";
  import {loadArea} from 'api/invokeInterface'
  import ElRow from "element-ui/packages/row/src/row";
  import ElCol from "element-ui/packages/col/src/col";
  import FormTitle from 'common/vue/title'
  import backTop from 'components/backToTop/backToTop'
  import {getCurrentDate} from 'common/js/utils'
  import {validForm} from 'common/js/vaildations'
  import {regionData} from 'element-china-area-data'
  import {getArchive, registForm, getEditRegister} from 'api/invokeInterface'
  import Qs from 'qs'
  import store from 'store'

  export default {
    components: {
      ElCol,
      ElRow,
      ElInput,
      ElFormItem,
      FormTitle,
      backTop
    },
    data: function () {
      return {
        regionData:regionData,
        ruleForm: {
          regType: '',
          enterpriseZh: '',
          enterpriseEn: '',
          registZh: '',
          registEn: '',
          registPostcode: '',
          workPostcode: '',
          workplaceZh: '',
          workplaceEn: '',

          adminarea: [],
          adminareaCode: '',
          /*belongCenter:'',*/
          licenseCode: '',
          enterpriseType: '',
          currency: '',
          capital: '',
          economicType: '',
          economicTypeCode: '',

          leader: '',
          leaderTele: '',
          contact: '',
          contactTele: '',
          contactEmail: '',
          emailReceive: '',
          website: '',
          productInfo: '',

          invoiceTitle: '',
          taxNumber: '',
          taxAddr: '',
          openBank: '',
        },
        rules: {
          regType: [{required: true, message: '', trigger: 'change'}],
          enterpriseZh: [{required: true, message: '', trigger: 'blur'}],
          registZh: [{required: true, message: '', trigger: 'blur'}],
          registPostcode: [{required: true, validator: validForm.postCode, trigger: 'blur'}],
          workplaceZh: [{required: true, message: '', trigger: 'blur'}],
          workPostcode: [{required: true, validator: validForm.postCode, trigger: 'blur'}],
          adminarea: [{required: true, message: '', trigger: 'change'}],
          belongCenter: [{required: true, message: '', trigger: 'change'}],
          licenseCode: [{required: true, message: '', trigger: 'blur'},
            {min: 15, max: 18, message: '15-18', trigger: 'blur'}],
          enterpriseType: [{required: true, message: '', trigger: 'change'}],
          capital: [{required: true, message: '', trigger: 'blur'},
            {type: 'number', message: '', trigger: 'blur'}],
          currency: [{required: true, message: '', trigger: 'change'}],

          leader: [{required: true, message: '', trigger: 'blur'}],
          leaderTele: [{required: true, validator: validForm.phone, trigger: 'blur'}],
          contact: [{required: true, message: '', trigger: 'blur'}],
          contactTele: [{required: true, validator: validForm.phone, trigger: 'blur'}],
          contactEmail: [{type: 'email', message: '', trigger: 'blur'}],
          website: [{type: 'url', message: '', trigger: 'blur'}],

          invoiceTitle: [{required: true, message: '', trigger: 'blur'}],
          taxNumber: [{required: true, Whitespace: false, message: '', trigger: 'blur'}],
        },
        archives: {},
        needUpload: false
      }
    },
    computed: {
      registDate() {
        return getCurrentDate();
      }
    },
    created() {
      this.initData();
      this.ruleForm.contactTele = store.get('user').phoneNumber;
      let uid = store.get('user').id;
      let from = this.$route.query.from;
      if (from === 'edit') {
        getEditRegister({
          userId: uid
        }).then((res) => {
            this.initValue(res);
        })
      }
    },
    methods: {
      initData() {
        getArchive().then((res) => {
            this.archives = this._normalizeArchive(res);
        })
      },
      initValue(data) {
        for (let key in this.ruleForm) {
          if (data.hasOwnProperty(key) && this.ruleForm.hasOwnProperty(key)) {
            this.ruleForm[key] = data[key];
          }
        }

      },
      _normalizeArchive(data) {
        let obj = {};
        data.forEach((item, index) => {
          obj[item.code] = item.archiveDetails;
        })
        return obj;
      },
      handleAdminareaChange(value) {
        this.ruleForm.adminareaCode = value[2];
      },
      onSubmit() {
        this.$refs.register.validate((valid) => {
          if (valid) {
            registForm(Qs.stringify(this.ruleForm)).then((res) => {
                this.$message({
                  message: '',
                  type: 'success'
                });
                setTimeout(()=>{this.$router.push('/upload')},3000);
            })
          }
        })

      },
      reset() {
        this.$refs.register.resetFields();
      }
    }
  }
</script>

<style lang="stylus">
  .form-box
    width 100%
    box-sizing border-box
    padding-right 200px

  .form-name
    margin 30px 0 20px 0
    padding-left: 40px;
    border-width 1px 0 0
    border-color #e6e6e6
    border-style solid
    legend
      padding 0 10px
      color #000
      font-size 20px
      font-weight 300

  .footer
    text-align center
</style>



