<template>
  <div class="detail">
     <el-form v-model="formData" label-position="left" inline class="demo-table-expand">
      <div class="block">
        <form-title name=""></form-title>
        <el-form-item label="">
          <span>{{ detail.regType }}</span>
        </el-form-item>
        <el-form-item label="():">
          <span>{{ detail.enterpriseZh }}</span>
        </el-form-item>
        <el-form-item label="():">
          <span>{{ detail.enterpriseEn }}</span>
        </el-form-item>
        <el-form-item label="():">
          <span>{{ detail.registZh }}</span>
        </el-form-item>
        <el-form-item label="():">
          <span>{{ detail.registEn }}</span>
        </el-form-item>
        <el-form-item label=":">
          <span>{{ detail.registPostcode }}</span>
        </el-form-item>
        <el-form-item label="::">
          <span>{{ detail.workPostcode }}</span>
        </el-form-item>
        <el-form-item label="():">
          <span>{{ detail.registPostcode }}</span>
        </el-form-item>
        <el-form-item label="():">
          <span>{{ detail.workplaceZh }}</span>
        </el-form-item>
        <el-form-item label="():">
          <span>{{ detail.workplaceEn }}</span>
        </el-form-item>
      </div>
      <div class="block">
        <form-title name=""></form-title>
        <el-form-item label="">
            <span>{{ detail.provice }} - {{ detail.city }} - {{ detail.town }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.adminareaCode }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.licenseCode }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.enterpriseType }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.currency }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.capital }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.economicType }}</span>
          </el-form-item>
      </div>
      <div class="block">
        <form-title name=""></form-title>
        <el-form-item label="">
            <span>{{ detail.leader }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.leaderTele }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.contact }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.contactTele }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.contactEmail }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.website }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.productInfo }}</span>
          </el-form-item>
      </div>
      <div class="block">
        <form-title name=""></form-title>
        <el-form-item label="">
            <span>{{ detail.invoiceTitle }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.taxNumber }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.taxAddr }}</span>
          </el-form-item>
          <el-form-item label="">
            <span>{{ detail.openBank }}</span>
          </el-form-item>
      </div>
      <div class="form-cmp">
        <el-form-item class="item" prop="memo">
          <el-input
            type="textarea"
            :rows="3"
            placeholder=""
            v-model="formData.memo">
          </el-input>
        </el-form-item>
        <el-form-item class="item" prop="pass">
          <el-radio v-model="formData.pass" name="pass" label="2"></el-radio>
          <el-radio v-model="formData.pass" name="pass" label="3"></el-radio>
        </el-form-item>
      </div>
      <div class="btns">
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="onCancel"></el-button>
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>
<script type="text/ecmascript-6">
  import FormTitle from 'common/vue/title'
  import {getDetail} from 'api/invokeInterface'
  import ElForm from "../../../node_modules/element-ui/packages/form/src/form.vue";
  import ElFormItem from "../../../node_modules/element-ui/packages/form/src/form-item.vue";

  export default {
    props: {
      id: {
        type: Number
      },
      name:{
        type: String
      }
    },
    components: {
      ElFormItem,
      ElForm,
      FormTitle
    },
    data() {
      return {
        detail: {},
        formData: {
          memo: '',
          pass: 0
        }
      }
    },
    created() {
      getDetail({
        id: this.id
      }).then((res) => {
        this.detail = res;
        console.log(res);
      })
    },
    methods: {
      onSubmit() {
        this.formData['id'] = this.id;
        this.formData['name'] = this.name;
        this.$emit('approve',this.formData);
      },
      onCancel(){
        this.$emit('cancel');
      }
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .detail
    color #000
    .block
      font-size 0

  .el-form-item
    margin-bottom 0
    width 48%
    label

      width 110px
      color #99a9bf

  .form-cmp
    margin-top 20px
    text-align left
    .item
      width 50%
      .el-form-item__content
        width 50%
  .btns
    text-align center
</style>



